import React from "react";

import styled from "styled-components";
import { Header } from "../components/Header";
import { Profile } from "../components/Profile";
import { ResponsiveStackGrid } from "../components/ResponsiveStackGrid";
import { ShowcaseCard } from "../components/ShowcaseCard";
import { showcases } from "../utils/showcases";

const Container = styled.div`
  padding-top: 88px;
  padding-bottom: 100px;
`;

const Footer = styled.div`
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 0 24px;
  background: #f8f8f8;
  border-top: 1px solid #eee;
  z-index: 10;
`;

const Index = () => {
  return (
    <Container>
      <Header />
      <ResponsiveStackGrid items={showcases} interval={200}>
        {(onSizeChange, appearedShowcases) =>
          appearedShowcases.map(showcase => (
            <ShowcaseCard
              key={showcase.key}
              showcase={showcase}
              onSizeChange={onSizeChange}
            />
          ))
        }
      </ResponsiveStackGrid>
      <Footer>
        <Profile />
      </Footer>
    </Container>
  );
};

export default Index;
